.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.form-group.required > label:after {
  content: "*";
  color: #e3342f;
}

.form-control::placeholder {
  opacity: 0.5;
}

.form-control[type=file] {
  height: auto;
}

.form-control-multiselect.is-invalid ~ .invalid-feedback {
  display: block;
}
.form-control-multiselect.is-invalid .multiselect__tags {
  border-color: #e3342f;
}

.custom-control.horizontal-radio {
  display: inline;
  margin-right: 1em;
}

.table-responsive-card th, .table-responsive-card td {
  vertical-align: middle;
}
.table-responsive-card th {
  border-top: 0;
}
.table-responsive-card td.actions {
  text-align: right;
}
.table-responsive-card td.actions a {
  padding: 0 0.5em;
}
.table-responsive-card td .td-background {
  padding: 0.75rem;
}
.table-responsive-card th.check, .table-responsive-card td.check {
  width: 0;
}
.table-responsive-card td, .table-responsive-card th {
  hyphens: auto;
}
.table-responsive-card .responsive-td-background {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0 0.75rem;
}
@media (max-width: 991.98px) {
  .table-responsive-card thead, .table-responsive-card td.check, .table-responsive-card th.check {
    display: none;
  }
  .table-responsive-card tr {
    border: 1px solid #dee2e6;
    margin-bottom: 1em;
    display: block;
    padding: 0.5em 0;
  }
  .table-responsive-card th, .table-responsive-card td {
    display: block;
    border: none;
    padding: 0 0.75em;
    margin: 0.5em 0;
  }
  .table-responsive-card td:before {
    content: attr(data-label) "\a";
    white-space: pre;
    font-weight: bold;
  }
  .table-responsive-card td.actions {
    text-align: left;
  }
  .table-responsive-card td.actions a:first-child {
    padding-left: 0;
  }
  .table-responsive-card .responsive-td-background {
    position: initial;
    display: block;
    padding: 0 0.75rem;
    margin: 0 -0.75rem;
  }
}

@media (min-width: 992px) {
  .align-items-center-lg {
    align-items: center;
  }
}
[aria-controls*=collapse] {
  cursor: pointer;
}
[aria-controls*=collapse] .fa-caret-down::before {
  transition: transform 0.3s ease;
  transform: rotate(180deg);
  transform-origin: 50% 58%;
  display: inline-block;
}
[aria-controls*=collapse].collapsed .fa-caret-down::before {
  transform: rotate(0deg);
}

